.content {
  --column-count: 1;
  padding: 50px 0 72px;
  display: grid;
  row-gap: 46px;
  column-gap: 48px;
  grid-template-columns: repeat(var(--column-count), $card-width);
  justify-content: center;
  align-items: center;
  perspective: 100%;

  @media (min-width: 488px) {
    --column-count: 2;
  }

  @media (min-width: 768px) {
    --column-count: 3;
  }

  @media (min-width: 1024px) {
    --column-count: 4;
  }
}
